html, body {
  height: 100%; }

body {
  padding: 0;
  margin: 0;
  font-weight: 350;
  font-family: "MuseoSansCyrl", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #000000;
  background-color: #ffffff; }

a:hover {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0; }

.slick-list:focus {
  outline: none; }

.slick-list.dragging {
  cursor: pointer;
  cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.slick-track:before,
.slick-track:after {
  display: table;
  content: ''; }

.slick-track:after {
  clear: both; }

.slick-loading .slick-track {
  visibility: hidden; }

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px; }

[dir='rtl'] .slick-slide {
  float: right; }

.slick-slide img {
  display: block; }

.slick-slide.slick-loading img {
  display: none; }

.slick-slide.dragging img {
  pointer-events: none; }

.slick-initialized .slick-slide {
  display: block; }

.slick-loading .slick-slide {
  visibility: hidden; }

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.wrapper {
  max-width: 1440px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box; }

.title {
  font-size: 34px;
  line-height: 44px;
  font-weight: bold;
  color: #000000; }

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 60px;
  font-size: 24px;
  line-height: 28px;
  color: #ffffff;
  text-decoration: none;
  background-color: #ee8800;
  border: 2px solid #ee8800;
  border-radius: 10px;
  box-sizing: border-box;
  cursor: pointer;
  outline: none;
  transition: 0.5s; }
  .button:hover {
    color: #ee8800;
    background-color: #ffffff; }
  .button:active {
    opacity: 0.5;
    transition: 0.1s; }
  .button:focus {
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2); }

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  transition: 0.5s; }
  .popup--active {
    opacity: 1;
    visibility: visible; }
    .popup--active .popup__content {
      transform: translate(0, 0);
      opacity: 1; }
  .popup__container {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100%;
    padding: 30px 10px; }
  .popup__content {
    position: relative;
    max-width: 560px;
    padding: 30px 40px 48px;
    background: #ffffff;
    transition: 0.5s;
    transform: translate(0, -100%);
    opacity: 0; }
  .popup__title {
    margin-bottom: 32px;
    font-size: 32px;
    line-height: 45px;
    color: #000000; }
    .popup__title--little {
      font-size: 20px;
      line-height: 28px; }
  .popup__row {
    margin-bottom: 24px; }
  .popup__close {
    position: absolute;
    right: 25px;
    top: 25px;
    cursor: pointer; }
    .popup__close-icon {
      fill: red;
      transition: 0.5s; }
      .popup__close-icon:hover {
        fill: #333333; }
  .popup__list {
    padding-left: 20px; }
  .popup__item {
    margin-bottom: 20px;
    font-weight: bold;
    line-height: 24px;
    color: #0044bb; }
  .popup__text {
    font-weight: normal;
    color: #000000; }
  .popup__input-box {
    position: relative;
    margin-bottom: 20px; }
    .popup__input-box label {
      position: relative;
      display: block;
      padding-left: 30px;
      font-size: 14px;
      line-height: 18px;
      cursor: pointer; }
      .popup__input-box label:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 16px;
        height: 16px;
        border: 1px solid #0044bb;
        box-sizing: border-box; }
    .popup__input-box input:checked + label:after {
      content: "";
      position: absolute;
      left: 5px;
      top: 4px;
      width: 7px;
      height: 5px;
      transform: rotate(135deg);
      border-right: 1px solid #0044bb;
      border-top: 1px solid #0044bb;
      box-sizing: border-box; }
  .popup__buttons {
    display: flex;
    justify-content: center; }
  .popup__button {
    font-size: 18px;
    color: #fff;
    padding: 14px 30px;
    background: #0f54b9;
    border: 1px solid #0f54b9;
    border-radius: 4px;
    transition: .5s;
    cursor: pointer;
    margin-top: 50px; }
    .popup__button:hover {
      background: #ffffff;
      color: #0f54b9; }
    .popup__button--white {
      margin-left: 20px;
      background: #ffffff;
      color: #0f54b9; }
      .popup__button--white:hover {
        color: #fff;
        background: #0f54b9; }
    .popup__button--back {
      margin-top: 0; }
  .popup__error-message {
    position: absolute;
    bottom: -20px;
    left: 30px;
    font-size: 12px;
    line-height: 14px;
    color: red;
    opacity: 0;
    transition: 0.5s; }
    .popup__error-message.popup__error-massage--show {
      opacity: 1; }
  .popup__link {
    color: #0f54b9;
    text-decoration: none;
    border-bottom: 1px solid #0f54b9; }
    .popup__link:hover {
      border-bottom: 1px solid transparent; }
  .popup__box {
    margin: 40px 0 20px; }
  .popup__text {
    margin: 0 0 10px;
    text-align: center; }

.main__title {
  position: relative;
  width: 750px;
  margin: 0 auto;
  padding-top: 300px;
  font-size: 34px;
  text-align: center; }

.icon-address {
  display: flex;
  justify-content: center;
  align-items: center;
  fill: pink; }

.slider {
  width: 800px;
  margin: 0 auto; }

.slider__item {
  padding-top: 56.25%; }
